import { useState, useEffect } from "react";
import axios from "axios";

//  :     
export const useData = () => {
  const [regionData, setRegionData] = useState([]);
  const [modelInfo, setModelInfo] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        
        // GitHub Pages  URL  ()
        const isLocal = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
        const baseUrl = isLocal ? '' : 'https://yundo-git.github.io/smart-farm-web';
        
        console.log('Base URL:', baseUrl);
        
        //   
        const timestamp = new Date().getTime();
        
        try {
          //    
          const regionResponse = await axios.get(
            `${baseUrl}/region_settlement_data.json?t=${timestamp}`, 
            {
              headers: {
                'Cache-Control': 'no-cache, no-store, must-revalidate',
                'Pragma': 'no-cache',
                'Expires': '0'
              }
            }
          );
          var regionResult = regionResponse.data;
          console.log('   :', regionResult.length > 0 ? ' ' : ' ');
        } catch (regionError) {
          console.error("   :", regionError);
          regionResult = getDummyData();
          console.log("  ");
        }

        try {
          //   
          const modelResponse = await axios.get(
            `${baseUrl}/settlement_prediction_model.json?t=${timestamp}`, 
            {
              headers: {
                'Cache-Control': 'no-cache, no-store, must-revalidate',
                'Pragma': 'no-cache',
                'Expires': '0'
              }
            }
          );
          var modelResult = modelResponse.data;
          console.log('   :', Object.keys(modelResult).length > 0 ? ' ' : ' ');
        } catch (modelError) {
          console.error("   :", modelError);
          modelResult = getDummyModelInfo();
          console.log("   ");
        }

        setRegionData(regionResult);
        setModelInfo(modelResult);

        console.log(`${regionResult.length}    `);
        console.log("   :", modelResult);
      } catch (err) {
        console.error("  :", err);
        setError(err.message);

        //   
        setRegionData(getDummyData());
        setModelInfo(getDummyModelInfo());

        console.log("   ");
      } finally {
        setLoading(false);
      }
    };

    loadData();
  }, []);

  return { regionData, modelInfo, loading, error };
};

